import { Component, OnDestroy, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { ConfiguracionCompromisoModel } from 'src/app/commons/models/gestion-academica/configuracion-compromiso.model';
import { ConfiguracionCompromisoService } from 'src/app/commons/services/configuracion-compromiso.service';
import { TipoAcademicoService } from 'src/app/commons/services/tipo-academico.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { guardarConfiguracionCompromisos, paginacionConfiguracionCompromisos, resetConfiguracionCompromisos } from 'src/app/store/actions/configuracion-compromiso.actions';
import { AppState } from 'src/app/store/app.reducer';
import { ConfigCompromisosModule } from './config-compromisos.module';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-config-compromisos',
  templateUrl: './config-compromisos.component.html',
  styles: [
  ]
})
export class ConfigCompromisosComponent implements OnInit, OnDestroy {
  public configCompromiso: ConfigCompromisosModule = new ConfigCompromisosModule();
  private _subscriptionConfigCompromiso: Subscription;
  public listaConfigCompromiso: ConfigCompromisosModule[];
  public loaded: boolean = false;
  public loading: boolean = false;
  public loadingBotonActivar: boolean = false;
  public loadingBotonDesactivar: boolean = false;
  public edit: boolean = false;
  public auxiliar: string;
  @BlockUI() blockUI: NgBlockUI;

  //-----------
  public page: number = 1;
  public pageSize: number = 10;
  public totalElements: number;
  constructor(
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
    public tipoAcademicoService: TipoAcademicoService,
    public configCompromisoService: ConfiguracionCompromisoService,
  ) {
    this.appService.pageTitle = 'Mantenedor de Edición de Compromisos';
  }

  ngOnInit(): void {
    this._configCompromisosState();
    this.listarConfigCompromiso();
  }

  ngOnDestroy(): void {
    this._subscriptionConfigCompromiso.unsubscribe();
  }

  private _configCompromisosState() {
    this._subscriptionConfigCompromiso = this.store.select("configCompromiso").subscribe(
      (state) => {
        if (state.searchPagination != null) {
          this.page = state.searchPagination.page;
          this.pageSize = state.searchPagination.records;

        }
        if (state.loaded && state.paginacionConfiguracionCompromiso) {
          this.loaded = state.loaded
          this.listaConfigCompromiso = state.paginacionConfiguracionCompromiso.content;
          this.totalElements = state.paginacionConfiguracionCompromiso.totalElements;
          this.blockUI.stop();
        }
        if (state.configuracionCompromiso && state.loaded) {
          this.edit ? this.alertService.successSwal(`Se ha editado correctamente el indicador`) : this.alertService.successSwal(`Se ha registrado correctamente el indicador`);
          this.store.dispatch(resetConfiguracionCompromisos());
          this.edit = false;
          this.loading = false;
          this.listarConfigCompromiso();
          this.blockUI.stop()
        }
        if (state.configuracionCompromiso && state.loaded) {
          state.configuracionCompromiso.isEdit ? this.alertService.successSwal(`Se ha activado correctamente la edición de compromisos para esta facultad`) : this.alertService.successSwal(`Se ha desactivado la edición de compromisos para esta facultad`);
          this.store.dispatch(resetConfiguracionCompromisos());
          this.edit = false;
          this.listarConfigCompromiso();
          this.blockUI.stop()
        }
      }
    );
  }

  listarConfigCompromiso() {
    this.listaConfigCompromiso = [];
    const searchPagination: SearchPagination<string> = {
      page: this.page,
      records: this.pageSize,
      seek: "",
    };
    this.blockUI.start("Cargando...");
    this.store.dispatch(paginacionConfiguracionCompromisos({ searchPagination })
    );
  }

  desactivarTodos() {
    Swal.fire({
      title: '¿Desea desactivar la edición para todas las facultades?',
      text: "Esta acción no se puede deshacer",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      reverseButtons: true,
      cancelButtonText: 'Cancelar',
    }).then((result) => {
      if (result.isConfirmed) {
        this.loadingBotonDesactivar = true;
        this.configCompromisoService.desactivarAllConfiguracion().subscribe((value) => {
          if (value) {
            this.alertService.successSwal("Se ha desactivado la edición de los compromisos en todas las facultades")
            this.listarConfigCompromiso();
            this.loadingBotonDesactivar = false;

          }
        }, ((error) => {
          this.alertService.errorSwal("Ha ocurrido un error inseperado al momento de desactivar la edición de los compromisos en todas las facultades")
          this.loadingBotonDesactivar = false;

        }))
      }
    })

  }

  activarTodos() {
    Swal.fire({
      title: '¿Desea activar la edición para todas las facultades?',
      text: "Esta acción no se puede deshacer",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.loadingBotonActivar = true;
        this.configCompromisoService.activarAllConfiguracion().subscribe((value) => {
          if (value) {
            this.alertService.successSwal("Se ha activado la edición de los compromisos en todas las facultades")
            this.listarConfigCompromiso();
            this.loadingBotonActivar = false;

          }
        }, ((error) => {
          this.alertService.errorSwal("Ha ocurrido un error inseperado al momento de activar la edición de los compromisos en todas las facultades")
          this.loadingBotonActivar = false;

        }))
      }
    })

  }

  cambiarEstado(configuracionCompromiso: ConfiguracionCompromisoModel) {
    let cambio = 'Desactivarás';
    if (!configuracionCompromiso.isEdit) {
      cambio = 'Activarás'
    }
    Swal.fire({
      title: '¿Estás seguro/a?',
      text: cambio + " la edición de compromisos para la " + configuracionCompromiso.facultad.nombre,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true,
    }).then((result) => {
      if (result.isConfirmed) {
        this.store.dispatch(guardarConfiguracionCompromisos({ configuracionCompromiso: configuracionCompromiso }))
      }
    })
  }

}
